import React from 'react'

import { Web3ReactProvider } from '@web3-react/core'
import { Web3Provider } from '@ethersproject/providers'

import RedeemPage from './redeem_page/page'
import ShowcasePage from './showcase_page/page'
import AuctionsPage from './auctions_page/page'
import SaltSeasonsPage from './saltseasons/index'
import SaltSeasonCardsPage from './saltseason_cards/index'

function getLibrary(provider: any): Web3Provider {
  const library = new Web3Provider(provider)
  return library
}

function AppWrapper() {
  return (
    <Web3ReactProvider getLibrary={getLibrary}>
      {/* <RedeemPage/> */}
      {/* <ShowcasePage/> */}
      {/* <AuctionsPage/> */}
      {/* <SaltSeasonsPage/> */}
      <SaltSeasonCardsPage/>
    </Web3ReactProvider>
  )
}

export default AppWrapper
