import * as Firebase from '../services/firebase'

export type SaltSeason = {
  id: string
  number: string
  title: string
  posterUrls: string[]
  chainId: 1 | 42
  erc721ContractAddress: string
  sellerContractAddress: string
  tokenIds: number[]
  descriptionHtml: string
  // purchasedItems: number
}

const collection = Firebase.firestore.collection('saltseasons')

export async function indexByChainId(chainId: number) {
  const snapshot = await collection.where('chainId', '==', chainId).get()
  const items = snapshot.docs.map(doc => {
    var ss = doc.data() as SaltSeason
    ss.id = doc.id
    return ss
  })
  return items
}

export async function update(ss: SaltSeason) {
  const doc = collection.doc(ss.id)
  await doc.set(ss)
}